.CompactCard{
    display: flex;
    flex: 1;
    height: 7rem!important;
    border-radius: .7rem;
    color: white;
    position: relative;
    cursor: pointer;
}

.CompactCard:hover{
    box-shadow: none;
}

.radialBar{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: .5rem;
    padding-left: .75rem;
    padding-bottom: .15rem;
    margin-top: .5rem;
    
}

.CircularProgressbar{
    width: 4rem !important;
    overflow: visible;
}

.CircularProgressbar-path{
    stroke: white !important;
    stroke-width: 12px !important;
    filter: drop-shadow(2px 4px 6px white)
}

.CircularProgressbar-trail{
    display: none;
}

.CircularProgressbar-text{
    fill: white !important;
}

.radialBar>span{
    font-size: 17px;
    font-weight: bold;
}

.detail{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    padding: 1rem 1.25rem 1rem 1.25rem;
}

.detail>span:nth-child(2){
    font-size: 22px;
    font-weight: bold;
}

.detail>span:nth-child(3){
    font-size: 12px;
}

.ExpandedCard{
    position: absolute;
    width: 60%;
    height: 70vh;
    z-index: 9;
    left: 13rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}

.ExpandedCard>spam:nth-of-type(1){
    color: white;
    font-size: 26px;
    font-weight: bold;
    text-shadow: 0px 0px 15px white;
}

.ExpandedCard>spam:nth-of-type(2){
    color: rgb(223, 222, 222);
    font-size: 16px;
}

@media screen and (max-width: 1200px){
    .ExpandedCard{
        top: 2rem;
        height: 70vh;
        left: 6rem;
    }
}